
(() => {    const refs = {      openMenuBtn: document.querySelector('[data-menu-open]'),      closeMenuBtn: document.querySelector('[data-menu-close]'),      menu: document.querySelector('[data-menu]'),      body: document.querySelector('[data-body]')    };      refs.openMenuBtn.addEventListener('click', toggleModal);    refs.closeMenuBtn.addEventListener('click', toggleModal);      function toggleModal() {      refs.menu.classList.toggle('is-open');      refs.body.classList.toggle("body--overflow");    }  })();